.cart--bigDiv{
    min-height: calc(100vh - 160px);
    padding: 80px 100px 0 100px;
}

.cart-bigDivBackground{
    background: rgba(57, 55, 72, 0.22);
}
.individual--product{
    border-top: 1px solid #E5E5E5;
}

.left-cartDiv{
    padding: 24px 0 24px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.title-cartP{
    margin: 0;
    padding: 0;
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    text-transform: uppercase;
    color: #1D1F22;
    width: 100%;
    padding-bottom: 55px;
}
.att-divCart{
    display: flex;
    gap: 8px;
}
.swatch-cartDivv{
    height: 32px;
    width: 32px;
    box-sizing: border-box;
    box-shadow: 0 0 0 1px #ffffff inset;
}
.swatch-cartDivv-a{
    box-shadow: 0 0 0 1px #bebdbd inset;
}
.swatch-cartDivvBorder{
    border: 1px solid #5ECE7B;
}

.alt--divs{
    height: 45px;
    width: 63px;
    background: #FFFFFF;
    border: 1px solid #1D1F22;
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    letter-spacing: 0.05em;
    color: #1D1F22;
}

.alt--divsAlt{
    background: #1D1F22;
    border: 1px solid #1D1F22;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
}

.cart--divBrad{
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 27px;
    display: flex;
    align-items: center;
    color: #1D1F22;
    padding-bottom: 16px;
}

.cart--divName{
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 27px;
    display: flex;
    align-items: center;
    color: #1D1F22;
    padding-bottom: 20px;
}

.cart--divPrices{
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #1D1F22;
    padding-bottom: 20px;
}

.cart--attName{
    font-family: 'Roboto Condensed', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #1D1F22;
    text-transform: uppercase;
    padding-bottom: 7px;
}
.all-attDivs{
    padding-bottom: 16px;
}
.all-attDivs:last-child{
    padding :0;
}

.individual--product{
    display: flex;
    justify-content: space-between;
}

.rigth-cartDiv{
    display: flex;
    padding: 24px 0 24px 0;
}

.buttons-cartDiv{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}

.right--imgDiv{
    width: 200px;
    height: 288px;
    padding-left: 24px;
    display: flex;
    position: relative;
}

.right--imgImg{
    max-width: 100%;
    max-width: 100%;
    object-fit: contain;
}

.button-addCart,
.button-subCart{
    height: 45px;
    width: 45px;
    position: relative;
    border: 1px solid #1D1F22;
    cursor: pointer;
}

.button-subCart:active{
    border: 1px solid red;
}
.button-addCart:active{
    border: 1px solid green;
}

.plus-SVG{
    position: absolute;
    left: 33.33%;
    right: 33.33%;
    top: 50%;
    bottom: 50%;
    z-index: -1;
}
.plusTwo-SVG{
    position: absolute;
    left: 50%;
    right: 50%;
    top: 33.33%;
    bottom: 33.33%;
    z-index: -1;
}
.unselectable{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@media (max-width: 764px){
    .cart--bigDiv{
        padding: 80px 10px 0 10px;
    }
    .cart--divBrad,
    .cart--divPrices,
    .cart--divName,
    .cart--attName{
        font-size: 4vw;
    }
    .alt--divs{
        padding: 1px;
        height: auto;
        width: auto;
        min-height: 10px !important;
        min-width: 10px !important;
    }
    .cart--attName{
        text-align: left;
    }

    .button-addCart,
    .button-subCart{
        height: 20px;
        width: 20px;
    }
    
    .plus-SVG{
        left: 10%;
    }
    .plusTwo-SVG{
        top: 12%;
    }
    .left-cartDiv{
        width: 50vw;
    }
    .swatch-cartDivv {
        height: 20px;
        width: 20px;
    }
    .button-cartOrder{
        margin-left: 14%;
    }
    .buttons-cartDiv{
        padding: 0 10px;
    }
    .right--imgDiv{
        padding-left: 0;
    }
    
}